@font-face {
  font-family: "Inter";
  font-weight: 100 900;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/Inter-VariableFont_opsz,wght.woff2") format("woff2-variations"), url("/fonts/Inter-VariableFont_opsz,wght.woff") format("woff-variations"), url("/fonts/Inter-VariableFont_opsz,wght.ttf") format("truetype-variations");
}

@font-face {
  font-family: "Inter";
  font-weight: 100 900;
  font-style: italic;
  font-display: swap;
  src: url("/fonts/Inter-Italic-VariableFont_opsz,wght.woff2") format("woff2-variations"), url("/fonts/Inter-Italic-VariableFont_opsz,wght.woff") format("woff-variations"), url("/fonts/Inter-Italic-VariableFont_opsz,wght.ttf") format("truetype-variations");
}

@font-face {
  font-family: "Golos";
  font-weight: 100 900;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/GolosText-VariableFont_wght.woff2") format("woff2-variations"), url("/fonts/GolosText-VariableFont_wght.woff") format("woff-variations"), url("/fonts/GolosText-VariableFont_wght.ttf") format("truetype-variations");
}

:root {
  /* Colors */
  --color-accent: #de127a;
  --color-accent-hover: color-mix(in srgb, var(--color-accent), #000 20%);
  --color-accent-active: color-mix(in srgb, var(--color-accent), #000 40%);
  --color-white: #fff;
  --color-white-hover: color-mix(in srgb, var(--color-white), #000 25%);
  --color-white-active: color-mix(in srgb, var(--color-white), #000 40%);
  --color-white-secondaty: #b8b8d6;
  --color-purple: #7d5c9c;
  --color-purple-hover: color-mix(in srgb, var(--color-purple), #000 20%);
  --color-purple-active: color-mix(in srgb, var(--color-purple), #000 40%);
  --color-yellow: #ffd409;
  --color-red: #e52c4b;
  --color-sky: #4ea8f3;
  --color-sky-hover: color-mix(in srgb, var(--color-sky), #000 25%);
  --color-sky-active: color-mix(in srgb, var(--color-sky), #000 40%);
  --color-green: #43a382;
  --color-pink: #de127a;
  /* Backgrounds colors */
  --bg-black: #040405;
  /* Border colors */
  --color-border-gray: rgba(255, 255, 255, 0.08);
  /* Transition */
  --trans-02s: all 0.2s ease;
  --trans-03s: all 0.3s ease;
  --trans-05s: all 0.5s ease;
  /* Gradients */
  --linear-reviews-bg: linear-gradient(
    140deg,
    color-mix(in srgb, color-mix (in srgb, #ffd409, transparent 40%), transparent 70%),
    color-mix(in srgb, color-mix(in srgb, #ffd409, transparent 40%), transparent 90%),
    transparent 50%
  );
}

*,
*::before,
*::after {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html {
  scroll-behavior: smooth;
  scroll-padding-top: 100px;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  background: center/cover no-repeat url("../img/grid-lines-bg.png");
  background-color: var(--bg-black);
  color: #fff;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

section:not(.overflow-visible) {
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: 1562px;
  max-width: 95%;
  margin: 0 auto;
}

.sec-p-y {
  padding: var(--sec-p-y) 0;
}

.sec-p-y--top {
  padding-top: var(--sec-p-y);
}

.sec-p-y--bottom {
  padding-bottom: var(--sec-p-y);
}

.sec-p-x {
  padding: 0 var(--sec-p-y);
}

.btn,
.btn-icon-white,
.btn-icon-purple,
.btn-white-pink,
.btn-white,
.btn-sky,
.btn-purple,
.btn-accent {
  --btn-bg: var(--color-accent);
  --btn-bg-hover: var(--color-accent-hover);
  --btn-bg-active: var(--color-accent-active);
  border: none;
  cursor: pointer;
  background-color: transparent;
  transition: var(--trans-02s);
  padding: 13px 21px;
  border-radius: 16px;
  font-family: "Golos", sans-serif;
  font-weight: bold;
  text-decoration: none;
  font-size: 16px;
  padding: 16px 40px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  text-align: center;
}

.btn:hover::before,
.btn-icon-white:hover::before,
.btn-icon-purple:hover::before,
.btn-white-pink:hover::before,
.btn-white:hover::before,
.btn-sky:hover::before,
.btn-purple:hover::before,
.btn-accent:hover::before {
  background: var(--btn-bg-hover);
}

.btn:active,
.btn-icon-white:active,
.btn-icon-purple:active,
.btn-white-pink:active,
.btn-white:active,
.btn-sky:active,
.btn-purple:active,
.btn-accent:active {
  transform: scale(0.95);
}

.btn:active::before,
.btn-icon-white:active::before,
.btn-icon-purple:active::before,
.btn-white-pink:active::before,
.btn-white:active::before,
.btn-sky:active::before,
.btn-purple:active::before,
.btn-accent:active::before {
  background: var(--btn-bg-active);
}

.btn::before,
.btn-icon-white::before,
.btn-icon-purple::before,
.btn-white-pink::before,
.btn-white::before,
.btn-sky::before,
.btn-purple::before,
.btn-accent::before {
  content: "";
  width: 100%;
  height: 100%;
  background: var(--btn-bg);
  position: absolute;
  left: 0;
  top: 0;
  transform: skewX(-10deg);
  border-radius: 15px;
  z-index: -1;
  transition: var(--trans-02s);
}

.btn-reset {
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

.btn-accent {
  --btn-bg: var(--color-accent);
  --btn-bg-hover: var(--color-accent-hover);
  --btn-bg-active: var(--color-accent-active);
  color: var(--c-white);
}

.btn-purple {
  --btn-bg: var(--color-purple);
  --btn-bg-hover: var(--color-purple-hover);
  --btn-bg-active: var(--color-purple-active);
  color: var(--c-white);
}

.btn-sky {
  --btn-bg: var(--color-sky);
  --btn-bg-hover: var(--color-sky-hover);
  --btn-bg-active: var(--color-sky-active);
  color: var(--c-white);
}

.btn-white {
  --btn-bg: var(--color-white);
  --btn-bg-hover: var(--color-white-hover);
  --btn-bg-active: var(--color-white-active);
  color: var(--color-purple);
}

.btn-white-pink {
  --btn-bg: var(--color-white);
  --btn-bg-hover: var(--color-white-hover);
  --btn-bg-active: var(--color-white-active);
  color: var(--color-pink);
}

.btn-icon-purple {
  --btn-bg: var(--color-purple);
  --btn-bg-hover: var(--color-purple-hover);
  --btn-bg-active: var(--color-purple-active);
  color: var(--color-white);
  padding: 0 !important;
}

.btn-icon-white {
  --btn-bg: var(--color-white);
  --btn-bg-hover: var(--color-white-hover);
  --btn-bg-active: var(--color-white-active);
  color: var(--color-purple);
  padding: 0 !important;
}

.btn-icon {
  min-width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: var(--trans-02s);
  color: transparent;
}

.btn-icon .swiper-navigation-icon {
  display: none;
}

.btn-icon:hover::before {
  background: var(--color-white-hover);
}

.btn-icon:active {
  transform: scale(0.95);
}

.btn-icon:active::before {
  background: var(--color-white-secondaty);
}

.btn-icon::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: var(--color-white);
  position: absolute;
  left: 0;
  top: 0;
  transform: skewX(-10deg);
  border-radius: 15px;
  z-index: -1;
  transition: var(--trans-02s);
}

.df {
  display: flex;
  align-items: center;
}

.df.col {
  flex-direction: column;
}

.df.col-rev {
  flex-direction: column-reverse;
}

.df.gap-5 {
  gap: 5px;
}

.df.gap-10 {
  gap: 10px;
}

.df.gap-15 {
  gap: 15px;
}

.df.gap-20 {
  gap: 20px;
}

.x-btn {
  --size: 36px;
  display: inline-grid;
  place-items: center;
  width: var(--size);
  height: var(--size);
  background: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
  border-radius: 6px;
  position: absolute;
  top: 20px;
  right: 20px;
  color: #333;
}

.x-btn:hover {
  color: #000;
  background: rgba(0, 0, 0, 0.04);
}

.x-btn:focus {
  outline: 3px solid rgba(20, 120, 255, 0.2);
  outline-offset: 3px;
  border-radius: 8px;
}

.x-btn::before,
.x-btn::after {
  content: "";
  position: absolute;
  width: 60%;
  height: 2px;
  background: currentColor;
  left: 20%;
  top: 50%;
  transform-origin: center;
}

.x-btn::before {
  transform: translateY(-50%) rotate(45deg);
}

.x-btn::after {
  transform: translateY(-50%) rotate(-45deg);
}

.burger {
  --size: 28px;
  --bar-height: 2px;
  --bar-color: #fff;
  position: relative;
  width: var(--size);
  height: var(--size);
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  transition: var(--trans-02s);
}

.burger span {
  display: block;
  width: 100%;
  height: var(--bar-height);
  background-color: var(--bar-color);
  border-radius: 4px;
}

.burger:hover {
  gap: 8px;
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}

.text-c-white {
  color: var(--color-white);
}

.text-c-white-sec {
  color: var(--color-white-secondaty);
}

.text-c-purple {
  color: var(--color-purple);
}

.text-c-yellow {
  color: var(--color-yellow);
}

.text-c-red {
  color: var(--color-red);
}

.text-c-sky {
  color: var(--color-sky);
}

.text-c-green {
  color: var(--color-green);
}

.text-c-pink {
  color: var(--color-pink);
}

.text-c-accent {
  color: var(--color-accent);
}

.img-cover img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.skewx--10 {
  --skewx-scale: 1;
  transform: skewX(-10deg) scale(var(--skewx-scale));
}

.skewx--10-content {
  transform: skewX(10deg);
}

.tab-pane {
  display: none;
}

.tab-pane.active {
  display: block;
}

.tab-button.active {
  background-color: var(--color-white);
  color: var(--color-accent);
}

.tabs-buttons {
  background: linear-gradient(-129deg, color-mix(in srgb, #040a0f, transparent 60%) 20%, color-mix(in srgb, #de127a, transparent 40%) 100%) color-mix(in srgb, #0e1923, transparent 60%);
  background-repeat: no-repeat;
}

.card-tech-before-gradient,
.card-tech-before-gradient-yellow,
.card-tech-before-gradient-sky,
.card-tech-before-gradient-pink,
.card-tech-before-gradient-purple {
  --cur-color: var(--color-accent);
  --cur-bg: color-mix(in srgb, #0e1923 40%, transparent);
  background: linear-gradient(22deg, color-mix(in srgb, color-mix(in srgb, var(--cur-color), transparent 40%), transparent 70%), color-mix(in srgb, color-mix(in srgb, var(--cur-color), transparent 40%), transparent 90%), transparent 50%) var(--cur-bg);
  backdrop-filter: blur(40px);
}

.card-tech-before-gradient.--top-l,
.--top-l.card-tech-before-gradient-yellow,
.--top-l.card-tech-before-gradient-sky,
.--top-l.card-tech-before-gradient-pink,
.--top-l.card-tech-before-gradient-purple {
  background: linear-gradient(140deg, color-mix(in srgb, color-mix(in srgb, var(--cur-color), transparent 40%), transparent 70%), color-mix(in srgb, color-mix(in srgb, var(--cur-color), transparent 40%), transparent 90%), transparent 50%) var(--cur-bg);
}

.card-tech-before-gradient-purple {
  --cur-color: var(--color-purple);
}

.card-tech-before-gradient-pink {
  --cur-color: var(--color-accent);
}

.card-tech-before-gradient-sky {
  --cur-color: var(--color-sky);
}

.card-tech-before-gradient-yellow {
  --cur-color: var(--color-yellow);
  --cur-bg: rgba(0, 0, 0, 0.5);
}

.border-gradient {
  --border-radius: 15px;
  --border-width: 1.3px;
  --border-gradient-deg: 0deg;
  --border-gradient-image: linear-gradient(45deg, #4f4100, #ffd409, #4f4100);
  --content-bg-color: #0a0b0d;
  position: relative;
  border-radius: var(--border-radius);
}

.border-gradient-border {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border-radius: var(--border-radius);
  border: var(--border-width) solid transparent;
  background-clip: content-box, border-box;
  background-origin: border-box;
  z-index: -2;
  background-image: linear-gradient(var(--border-gradient-deg), var(--content-bg-color), var(--content-bg-color)), var(--border-gradient-image);
}

.border-gradient-content {
  position: absolute;
  left: var(--border-width);
  top: var(--border-width);
  width: calc(100% - 2 * var(--border-width));
  height: calc(100% - 2 * var(--border-width));
  border-radius: var(--border-radius);
  background-color: var(--content-bg-color);
  backdrop-filter: blur(40px);
  z-index: -1;
  background-image: inherit;
  background: inherit;
}

body .swiper {
  overflow: visible;
}

.swiper-button-prev,
.swiper-button-next {
  position: relative;
}

.badge-clip {
  font-family: "Inter", sans-serif;
  font-style: italic;
  font-weight: 800;
  padding: 3px 24px 7px 24px;
  clip-path: polygon(0% 13.87%, 100% 0%, 86.06% 100%, 4.07% 86.96%);
}

.img-cover-skew {
  transform: skewX(10deg);
  max-width: 200%;
  object-fit: cover;
  width: 115%;
  height: 100%;
  position: absolute;
  right: -8%;
  top: 0;
  z-index: -1;
}

.header {
  background: linear-gradient(180deg, #000 0%, rgba(0, 0, 0, 0.75) 80%, transparent 100%);
}

.header.scrolled {
  padding: 10px 0;
}

.header__navbar::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  transform: skewX(-10deg);
  background-color: #090a0c;
  border: 1.5px solid var(--color-border-gray);
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}

.header-mobile.active .header-mobile__content {
  top: 0px;
}

.header-mobile__content {
  top: -18px;
}

.header-mobile .x-btn {
  right: 30px;
  top: 40px;
  color: var(--color-white);
}

.modal-wrapper {
  width: 100%;
  height: 100%;
  background-color: color-mix(in srgb, #000, transparent 50%);
  position: fixed;
  left: 0;
  top: 0;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  z-index: 100;
}

.modal-wrapper.active {
  opacity: 1;
  visibility: visible;
}

.modal-content {
  position: relative;
  width: 95%;
  max-width: 500px;
  padding: 30px;
  border-radius: 26px;
  background-color: #fff;
  text-align: center;
  box-shadow: 0px 0px 20px rgba(255, 255, 255, 0.6);
}

.modal-content__title {
  font-size: 36px;
  font-weight: 600;
}

.modal-content__subtitle {
  margin-top: 10px;
  font-size: 20px;
}

.modal-success .modal-content {
  padding-bottom: 190px;
}

.modal-success .modal-content__img {
  width: 250px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.modal-fform .sec-fform__form {
  width: 100%;
  padding: 0;
}

.modal-fform .sec-fform__form select,
.modal-fform .sec-fform__form input {
  max-width: 100%;
}

.modal-fform .sec-fform__form::before {
  content: none;
}

.teachers-block {
  width: 100%;
  max-width: 218px;
  background: #fff;
  border-radius: 50px;
  box-shadow: -10px 25px 33px color-mix(in srgb, var(--accent), transparent 88%);
  transition: var(--trans-03s);
}

.teachers-block:hover {
  transform: scale(1.05);
}

.teachers-block:nth-child(1) {
  margin-top: 110px;
}

.teachers-block:nth-child(1) .teachers-block__img {
  transform: translate(-30px, 0px);
}

.teachers-block:nth-child(2) {
  margin-top: -20px;
}

.teachers-block:nth-child(2) .teachers-block__img {
  transform: translate(-50px, 0px) scale(1.15);
}

.teachers-block:nth-child(3) {
  margin-top: 76px;
}

.teachers-block:nth-child(3) .teachers-block__img {
  transform: translate(-5px, 0px);
}

.teachers-block:nth-child(4) .teachers-block__img {
  transform: translate(-30px, 0px) scale(1.3);
}

.teachers-block__img {
  height: 180px;
  margin-bottom: 5px;
  width: 125%;
  max-width: 270px;
  position: relative;
}

.teachers-block__img img {
  width: 100%;
  max-height: 280px;
  object-fit: contain;
  position: absolute;
  bottom: 0;
  right: 0;
}

.teachers-block__body {
  padding: 30px;
  padding-top: 0;
}

.teachers-block__title {
  font-size: 32px;
  margin-bottom: 8px;
}

.teachers-block__subtitle {
  font-size: 18px;
  color: var(--c-secondary);
  margin-bottom: 25px;
}

.teachers-block__audio-sub {
  font-size: 14px;
  color: var(--c-secondary);
  margin-left: 8px;
  margin-top: 8px;
}

.teachers-block__audio {
  display: flex;
  align-items: center;
  gap: 6px;
  background-color: #fff5f5;
  border-radius: 50px;
  padding: 10px 20px;
  width: fit-content;
  background-color: #ffeeee;
  border-radius: 12px;
  padding: 12px 14px;
  width: 100%;
  min-height: 54px;
}

.teachers-block__audio.active .teachers-block__audio-play .pause {
  display: block;
}

.teachers-block__audio.active .teachers-block__audio-play .play {
  display: none;
}

.teachers-block__audio.active .wave-bar {
  animation: wave 1.5s infinite ease-in-out;
}

.teachers-block__audio-play .pause {
  display: none;
}

.teachers-block .audio-wave {
  display: flex;
  align-items: center;
  gap: 2px;
}

.teachers-block .wave-bar {
  display: block;
  width: 4px;
  height: 20px;
  background-color: #ff9999;
  border-radius: 2px;
  transition: all 0.1s ease-in-out;
}

.teachers-block .wave-bar:nth-child(1) {
  animation-delay: 1.6s;
  height: 15px;
}

.teachers-block .wave-bar:nth-child(2) {
  animation-delay: 1.3s;
  height: 14px;
}

.teachers-block .wave-bar:nth-child(3) {
  animation-delay: 1.3s;
  height: 12px;
}

.teachers-block .wave-bar:nth-child(4) {
  animation-delay: 0.2s;
  height: 13px;
}

.teachers-block .wave-bar:nth-child(5) {
  animation-delay: 0.5s;
  height: 7px;
}

.teachers-block .wave-bar:nth-child(6) {
  animation-delay: 2.4s;
  height: 25px;
}

.teachers-block .wave-bar:nth-child(7) {
  animation-delay: 2s;
  height: 23px;
}

.teachers-block .wave-bar:nth-child(8) {
  animation-delay: 2.2s;
  height: 9px;
}

.teachers-block .wave-bar:nth-child(9) {
  animation-delay: 0.8s;
  height: 7px;
}

.teachers-block .wave-bar:nth-child(10) {
  animation-delay: 1.4s;
  height: 20px;
}

.teachers-block .wave-bar:nth-child(11) {
  animation-delay: 1.9s;
  height: 28px;
}

.teachers-block .wave-bar:nth-child(12) {
  animation-delay: 0.6s;
  height: 10px;
}

.teachers-block .wave-bar:nth-child(13) {
  animation-delay: 0.1s;
  height: 30px;
}

.teachers-block .wave-bar:nth-child(14) {
  animation-delay: 0.7s;
  height: 18px;
}

.teachers-block .wave-bar:nth-child(15) {
  animation-delay: 0.3s;
  height: 19px;
}

.teachers-block .wave-bar:nth-child(16) {
  animation-delay: 0.3s;
  height: 11px;
}

.teachers-block .wave-bar:nth-child(17) {
  animation-delay: 0.9s;
  height: 12px;
}

@keyframes wave {
  0%, 100% {
    height: 10px;
  }

  50% {
    height: 30px;
  }
}

.sec-banner {
  padding-top: 100px;
  padding-bottom: 100px;
  min-height: 1040px;
  height: 100svh;
  background: center/cover no-repeat url("../img/banner-bg.png");
}

.sec-banner__title {
  text-align: center;
  font-size: 92px;
  font-weight: 900;
  text-transform: uppercase;
  font-style: italic;
  margin-top: 105px;
  display: none;
}

.sec-banner .container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.sec-banner__content {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
}

.sec-banner__contacts {
  margin-top: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.sec-banner__contacts-socs {
  display: flex;
  align-items: center;
  gap: 5px;
}

.sec-banner__contacts-soc {
  width: 50px;
  height: 50px;
  border-radius: 15px;
  background: var(--color-white);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--trans-02s);
}

.sec-banner__contacts-soc:hover {
  background: var(--color-white-hover);
}

.sec-banner__contacts-soc:active {
  --skewx-scale: 0.9;
}

.sec-banner__contacts-address {
  margin-left: auto;
  font-size: 24px;
  font-weight: 600;
  text-transform: uppercase;
}

.sec-banner__contacts-phones {
  margin-left: 15px;
  display: flex;
  flex-direction: column;
  font-size: 24px;
  font-weight: 600;
}

.sec-banner__contacts-phone {
  display: block;
  transition: var(--trans-02s);
}

.sec-banner__contacts-phone:hover {
  color: var(--color-white-hover);
}

.sec-banner__desc {
  margin-top: 32px;
  padding: 16px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border-top: 1px solid var(--color-white-secondaty);
  gap: 10px;
  font-size: 19px;
  font-weight: 500;
  color: var(--color-white-secondaty);
}

.sec-banner__desc-atmosphere {
  display: flex;
  align-items: center;
  gap: 16px;
}

.sec-banner__desc-atmosphere img {
  width: 38px;
  height: 38px;
  flex-shrink: 0;
}

